<template>
  <div>
    <ul class="infinite-list" v-infinite-scroll="load" style="overflow:auto">
      <li v-for="i in count" class="infinite-list-item" :key="i">
        <el-card class="box-card box">
          <div slot="header" class="clearfix">
            <span>{{i}}我的任务名称</span>
            <!-- <el-switch
              style="display: inline-block;float:right;"
              v-model="status"
              active-color="#13ce66"
              inactive-color="#ff4949"
              active-text="已完成"
              inactive-text="未完成"
              @click="changeStatus"
            ></el-switch> -->
            <!-- <span style="float: right;color:red;">{{status}}</span> -->
            <!-- <el-button style="float: right; padding: 3px 0" type="text">接单</el-button> -->
          </div>
          <div class="text item">{{taskContain}}</div>
        </el-card>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {
      count: 0,
      taskContain: "列表内容",
      status: true
    };
  },
  methods: {
    load() {
      if (this.count < 200) {
        this.count += 1;
      }
    },
    changeStatus(){
        this.status!=this.status
    }
  }
};
</script>

